import React, { useState, useEffect } from "react";
import "./style.css";
import axios from "axios";
import InputMask from "react-input-mask";
import { BASE_URL } from "../../../config/dev";

const Responsaveis = props => {
  const [cpf, setCpf] = useState("");
  const [senha, setSenha] = useState("");
  // const [nivel, setNivel] = useState("");

  useEffect(() => {
    // getUser();
    console.log("props.nivel");
    console.log(props.nivel);
    // setNivel(props.nivel);
  });

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}/pais/getPais.php`
    })
      .then(function(response) {
        // console.log("resp get:");
        // console.log(response.data);
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(JSON.parse(response.data));
        // console.log(JSON.parse(str[0]));
        // console.log(JSON.parse(data));
        for (let i = 0; i < str.length; i++) {
          let registro = str[0].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            matricula_aluno: registro[7],
            nivel: registro[8]
          });
        }
        // console.log(json);
      })
      .catch(function(response) {
        // console.log(response);
      });
  };

  const cpfMask = value => {
    return value
      .replace(/\D/g, "") // substitui qualquer caracter que nao seja numero por nada
      .replace(/(\d{3})(\d)/, "$1.$2") // captura 2 grupos de numero o primeiro de 3 e o segundo de 1, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de numero
      .replace(/(\d{3})(\d)/, "$1.$2")
      .replace(/(\d{3})(\d{1,2})/, "$1-$2")
      .replace(/(-\d{2})\d+?$/, "$1"); // captura 2 numeros seguidos de um traço e não deixa ser digitado mais nada
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 1) {
      retorno = (
        <section id="responsaveis-adm">
          <h2 className="margin-top-adm white-text">
            Cadastro de Responsáveis
          </h2>
          {/*<form className="row">*/}
          <form
            className="row"
            method="post"
            action={`${BASE_URL}pais/postPais.php`}
          >
            <div className="input-field col s12 m6">
              <input
                id="resp1"
                name="resp1"
                type="text"
                className="validate input-responsaveis white-text"
                maxLength="60"
              />
              <label>Responsável (1)</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="resp2"
                name="resp2"
                type="text"
                className="validate"
                maxLength="60"
              />
              <label>Responsável (2)</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="cpf"
                name="cpf"
                type="number"
                className="validate"
                maxLength="14"
              />
              <label>CPF</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="senha"
                name="senha"
                type="text"
                className="validate"
                maxLength="20"
              />
              <label>Senha</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="tel1"
                name="tel1"
                type="text"
                className="validate"
                maxLength="14"
              />
              <label>Telefone (1)</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="tel2"
                name="tel2"
                type="text"
                className="validate"
                maxLength="14"
              />
              <label>Telefone (2)</label>
            </div>
            <div className="input-field col s12 m6">
              <input
                id="matricula"
                name="matricula"
                type="text"
                className="validate"
                maxLength="20"
              />
              <label>Matrícula Aluno</label>
            </div>
            {/* <input
          type="text"
          name="cpf"
          id="cpf"
          placeholder="CPF"
          onChange={e => setCpf(e.target.value)}
        />
        <input
          type="text"
          name="senha"
          id="senha"
          placeholder="Senha"
          onChange={e => setSenha(e.target.value)}
        /> */}

            <div className="col s12 center">
              <button
                className="btn waves-effect waves-light center green"
                type="submit"
                name="action"
              >
                ENVIAR
                <i className="material-icons right">send</i>
              </button>
            </div>
            {/* <button type="submit" onClick={e => postUser(e)}>
          ENVIAR
        </button> */}
            {/* <button type="button" onClick={e => postUser(e)}>
          ENVIAR
        </button> */}
          </form>
        </section>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default Responsaveis;
