import React, { useEffect } from "react";
import { BASE_URL } from "../../config/dev";
import { BASE_URL_FRONT } from "../../config/dev";

const Midleware = () => {
  useEffect(() => {
    console.log("nivel teste");
    // console.log(localStorage.getItem('nivel'));
    console.log(window.location.search);
    const roughNivel = window.location.hash;
    console.log("roughNivel");
    console.log(roughNivel);
    if (roughNivel === "#/midle?nivel=0") {
      // user comum
      window.localStorage.setItem("nivel", 0);
      window.location.href = `${BASE_URL_FRONT}#/acesso`;
    } else if (roughNivel === "#/midle?nivel=1") {
      // user adm
      window.localStorage.setItem("nivel", 1);
      window.location.href = `${BASE_URL_FRONT}#/acesso`;
    } else {
      // erro (TRATAR E REDIRECIONAR)
      // window.location.href = `${BASE_URL_FRONT}`;
    }
  });
  return (
    <div>
      <p>Redirecionando...</p>
    </div>
  );
};

export default Midleware;
