import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import HomeResp from "./pages/responsavel/Home";
import HomeAdm from "./pages/adm/Home";
import * as serviceWorker from "./serviceWorker";
import "materialize-css/dist/css/materialize.min.css";
import Responsaveis from "./pages/adm/Responsaveis";
import ResponsaveisList from "./pages/adm/ResponsaveisList";
import Midleware from "./pages/midleware";
// import { BrowserRouter, Switch, Route, Router, Link } from "react-router-dom";
import { HashRouter, Switch, Route, Router, Link } from "react-router-dom";
// import { BrowserRouter as Router,
//   Switch,
//   Route,
//   Redirect } from "react-router-dom";

ReactDOM.render(
  <HashRouter>
    <Switch>
      <Route path="/" exact={true} component={App} />
      {/*<Route path="/responsavel" component={HomeResp} />*/}
      <Route path="/acesso" component={HomeAdm} />
      <Route path="/midle" component={Midleware} />
    </Switch>
  </HashRouter>
,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
