import React, { useState, useEffect } from "react";
import "./style.css";
import axios from "axios";
import InputMask from "react-input-mask";
import { BASE_URL } from "../../../config/dev";

const Responsaveis = props => {
  const [list, setList] = useState([]);
  const [editOrNot, setEditOrNot] = useState([]);

  useEffect(() => {
    getUser();
    // listPais();
  }, []);

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}pais/getPais.php`
    })
      .then(function(response) {
        // console.log("resp get:");
        // console.log(response.data);
        // console.log(JSON.parse(response.data));
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(str);

        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            matricula_aluno: registro[7],
            nivel: registro[8]
          });
        }
        // console.log(json);
        setList(json);
        // console.log("list");
        // console.log(list);
      })
      .catch(function(response) {
        // console.log(response);
      });
  };

  const listPais = () => {
    let rows = [];
    for (let i = 0; i < list.length; i++) {
      rows.push(
        <tr key={i}>
          <td>
            {/*<input
              id={"resp1_" + i}
              name={"resp1_" + i}
              type="text"
              className="validate"
              maxLength="60"
              value={list[i].nome_resp1}
              onChange={e => {
                // console.log("e");
                // console.log(e.target.value);
                // console.log("t");
                setList(e.target.value);
                // list[i].nome_resp1 = e.target.value;
              }}
            />*/}
            {list[i].nome_resp1}
          </td>
          <td>{list[i].nome_resp2}</td>
          <td>{list[i].cpf}</td>
          <td>{list[i].senha}</td>
          <td>{list[i].tel1}</td>
          <td>{list[i].tel2}</td>
          <td>{list[i].matricula_aluno}</td>
          <td>
            <form
              id="form_delete"
              className="row"
              method="post"
              action={`${BASE_URL}pais/deletePais.php`}
              onSubmit={e => {
                if (
                  window.confirm(
                    "Tem certeza que deseja excluir esse registro?"
                  )
                ) {
                  // console.log("excluido!");
                } else {
                  e.preventDefault();
                }
              }}
            >
              <input
                type="hidden"
                name={"id_delete"}
                id={"id_delete"}
                value={list[i].id}
              />
              <button
                type="submit"
                className="waves-effect waves-light btn btn-delete red"
              >
                EXCLUIR
              </button>
            </form>
            {/*<button
              className="waves-effect waves-light btn btn-edit blue"
              onClick={e => edit(e, i)}
            >
              EDITAR
            </button>*/}
          </td>
        </tr>
      );
    }
    return rows;
  };

  const edit = (e, id) => {
    // console.log("id");
    // console.log(id);
    document.getElementById("resp1_" + id).disabled = false;
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 1) {
      retorno = (
        <section id="responsaveis-adm">
          <h2 className="white-text">Listagem de Responsáveis</h2>
          <div className="container-table-resp container">
            <table className="table-list-responsaveis">
              <thead>
                <tr>
                  <th>Responsável (1)</th>
                  <th>Responsável (2)</th>
                  <th>CPF</th>
                  <th>Senha</th>
                  <th>Telefone (1)</th>
                  <th>Telefone (2)</th>
                  <th>Matrícula Aluno</th>
                  <th>Ações</th>
                </tr>
              </thead>

              <tbody>{listPais()}</tbody>
            </table>
          </div>
        </section>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default Responsaveis;
