import React, { useEffect, useState } from "react";
import "./style.css";
import HeaderAdm from "../../../components/HeaderAdm";
// import { BrowserRouter, Switch, Route } from "react-router-dom";
import Responsaveis from "../Responsaveis";
import ResponsaveisList from "../ResponsaveisList";
import Cardapio from "../Cardapio";
import Calendario from "../Calendario";
import Mural from "../Mural";
import Galeria from "../Galeria";

const HomeAdm = () => {
  const [ni, setNi] = useState(window.localStorage.getItem("nivel"));

  useEffect(() => {
    console.log("nivel");
    console.log(window.localStorage.getItem("nivel"));
    const nivel = window.localStorage.getItem("nivel");
    if (nivel != 1 && nivel != 0) {
      window.location.href = "/";
    }
    setNi(nivel);
  });

  return (
    <section className="home-adm">
      <HeaderAdm nivel={ni} />
      <Responsaveis nivel={ni} />
      <ResponsaveisList nivel={ni} />
      <Cardapio nivel={ni} />
      <Calendario nivel={ni} />
      <Mural nivel={ni} />
      {/*<Galeria nivel={ni} />*/}
    </section>
  );
};

export default HomeAdm;
